*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  font-family: "Open Sans";
  font-weight: 400;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
  height: 100%;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

.wrapper {
  max-width: 1441px;
  margin: 0 auto;
  font-family: "Open Sans";
}

.swiper-container {
  width: 100%;
}

.icon {
  width: 100%;
  height: 100%;
}

.icon-map {
  width: 21px;
  height: 18px;
}

.icon-logoicon {
  width: 55px;
  height: 67px;
}

.icon-workonghoursicon {
  min-width: 24px;
  min-height: 24px;
  width: 24px;
  height: 24px;
}

.icon-contactviber2, .icon-contactwatsapp2, .icon-contacttelegram2 {
  width: 25px;
  height: 27px;
}

.icon-phoneicon {
  width: 17px;
  height: 26px;
  transform: rotate(15deg);
}

.icon-repostbuttonicon {
  width: 21px;
  height: 16px;
}

.icon-gears {
  width: 24px;
  height: 17px;
}

.icon-contactviber1, .icon-contactwatsapp1, .icon-contacttelegram1, .icon-contactviber3, .icon-contactwatsapp3, .icon-contacttelegram3 {
  width: 16px;
  height: 17px;
}

.icon-checked {
  width: 57px;
  height: 56px;
  min-width: 57px;
  min-height: 56px;
}

.header {
  width: 100%;
}

.header__top-menu {
  width: 100%;
  height: 62px;
  background: #000000;
}

.header__top-logo {
  min-height: 133px;
  display: flex;
  align-items: center;
}

.header__bottom-menu {
  display: flex;
  align-items: center;
  background-color: #dcdcdc;
  width: 100%;
}

.top-menu__inner {
  max-width: 1281px;
  height: 100%;
  display: flex;
  align-items: center;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  height: 62px;
}

.top-menu__nav {
  margin-top: 9px;
  max-width: 725px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

@media (max-width: 785px) {
  .top-menu__nav {
    display: none;
  }
}

.top-menu__nav-item {
  text-decoration: none;
  font-size: 14px;
  color: #ffffff;
  font-weight: 400;
  font-family: Open Sans;
  text-transform: uppercase;
  border-bottom: 3px solid transparent;
}

.top-menu__nav-item:hover {
  text-decoration: none;
  color: #fff;
  border-color: #ff0000;
}

.top-menu__map {
  margin-top: 6px;
  display: flex;
  align-items: center;
  margin-left: auto;
  padding-left: 15px;
}

@media (max-width: 785px) {
  .top-menu__map {
    margin-left: 0px;
  }
}

.top-menu__map-link {
  text-decoration: none;
  font-size: 14px;
  color: #ffffff;
  font-weight: 400;
  font-family: Open Sans;
  margin-left: 5px;
  white-space: nowrap;
  border-bottom: 3px solid transparent;
}

.top-menu__map-link:hover {
  text-decoration: none;
  color: #fff;
  border-color: #ff0000;
}

.top-menu__mobile {
  visibility: hidden;
  pointer-events: none;
  position: absolute;
  left: 0px;
  top: 62px;
  height: 0px;
  display: flex;
  overflow: hidden;
  width: 100%;
  background: rgba(0, 0, 0, 0.932);
  z-index: 25;
  flex-direction: column;
  transition: height 0.5s linear, visibility 1s linear;
}

.top-menu__mobile-item {
  padding-top: 5px;
  padding-bottom: 5px;
}

.top-menu__mobile-item a {
  text-decoration: none;
  font-size: 14px;
  color: #ffffff;
  font-weight: 400;
  font-family: Open Sans;
  margin-left: 5px;
  white-space: nowrap;
  border-bottom: 3px solid transparent;
  text-transform: uppercase;
}

.top-menu__mobile-item:hover a {
  text-decoration: none;
  color: #fff;
  border-color: #ff0000;
}

.top-menu__mobile-bottom {
  display: flex;
  height: 100%;
  align-items: center;
  padding-top: 80px;
  flex-direction: column;
}

.top-menu__mobile_visible {
  height: 500px;
  visibility: visible;
  pointer-events: auto;
}

.top-menu__mobile-button {
  display: none;
  padding: 10px 20px;
  cursor: pointer;
  outline: none;
  border: none;
  align-items: center;
  background: none;
  margin-left: auto;
}

@media (max-width: 785px) {
  .top-menu__mobile-button {
    display: flex;
  }
}

.top-menu__mobile-button i {
  display: block;
  width: 23px;
  height: 3px;
  margin: 6px 0 6px 9px;
  position: relative;
}

.top-menu__mobile-button i::before {
  content: "";
  width: 100%;
  height: 3px;
  background: #fff;
  position: absolute;
  right: 0px;
  top: -9px;
  display: block;
}

.top-menu__mobile-button i span {
  width: 50%;
  height: 100%;
  background: #fff;
  position: absolute;
  right: 0px;
  display: block;
  transition: all 0.4s linear;
}

.top-menu__mobile-button i::after {
  content: "";
  width: 70%;
  height: 3px;
  right: 0px;
  bottom: -9px;
  background: #fff;
  position: absolute;
  display: block;
  transition: all 0.4s linear;
}

.top-menu__mobile-button:hover i span {
  width: 100%;
}

.top-menu__mobile-button:hover i::after {
  width: 100%;
}

.top-logo {
  background-image: linear-gradient(90deg, #f9f9f9 1px, transparent 1px);
  background-size: 3px 1px;
  background-position: -2px -2px;
}

.top-logo__inner {
  max-width: 1281px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  justify-content: space-between;
}

@media (max-width: 920px) {
  .top-logo__inner {
    flex-direction: column;
  }
}

.top-logo__logo {
  display: flex;
  padding-top: 20px;
  padding-bottom: 20px;
}

.top-logo__company-name {
  display: flex;
  flex-direction: column;
  padding-left: 5px;
  justify-content: center;
}

.top-logo__company-name span {
  font-size: 45px;
  line-height: 33px;
  color: #231f20;
  font-weight: 700;
  font-family: "Open Sans Condensed";
  text-transform: uppercase;
}

.top-logo__company-name span:nth-child(2) {
  font-size: 23px;
}

.top-logo__worktime {
  display: flex;
  padding-top: 20px;
  padding-bottom: 20px;
}

.top-logo__worktime-text {
  padding-left: 15px;
}

.top-logo__phones {
  padding-top: 20px;
  padding-bottom: 20px;
}

.top-logo__phones a:nth-child(1):not(.top-logo__phones-messenger) {
  display: flex;
  align-items: center;
}

.top-logo__phones a:nth-child(1):not(.top-logo__phones-messenger) span {
  padding-left: 5px;
}

.top-logo__phones a {
  font-size: 22px;
  line-height: 33px;
  color: #231f20;
  font-weight: 600;
}

.top-logo__phones a:nth-child(2):not(.top-logo__phones-messenger) {
  padding-left: 22px;
}

.top-logo__phones-messengers {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 105px;
  width: 100%;
  margin-left: auto;
}

.bottom-menu__inner {
  max-width: 1281px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  margin: 0 auto;
}

@media (max-width: 1145px) {
  .bottom-menu__inner {
    flex-wrap: wrap;
    justify-content: center;
  }
}

@media (max-width: 845px) {
  .bottom-menu__inner {
    flex-direction: column;
  }
}

.bottom-menu__nav {
  display: flex;
}

@media (max-width: 845px) {
  .bottom-menu__nav {
    flex-direction: column;
  }
}

.bottom-menu__nav-item {
  padding-top: 16px;
  padding-bottom: 16px;
  padding-left: 19px;
  padding-right: 19px;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;
}

.bottom-menu__nav-item:hover {
  background: #ff0000;
  color: #fff;
}

.bottom-menu__call {
  margin-left: auto;
  display: flex;
}

@media (max-width: 1145px) {
  .bottom-menu__call {
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 15px;
  }
}

@media (max-width: 400px) {
  .bottom-menu__call {
    flex-direction: column;
    align-items: center;
  }
}

.bottom-menu__call #request_call_input {
  width: 172px;
  height: 23px;
  border-radius: 4px;
  background-color: #ffffff;
  border: 1px solid #e6e6e6;
  border: none;
  outline: none;
  padding-left: 5px;
}

.bottom-menu__call #request_call_input::placeholder {
  font-size: 14px;
  color: #999999;
}

.bottom-menu__call #request_call_btn {
  border-radius: 4px;
  background-color: #ff0000;
  font-size: 14px;
  color: #ffffff;
  font-weight: 700;
  text-transform: uppercase;
  margin-left: 15px;
  padding-top: 2px;
  padding-bottom: 2px;
  padding-left: 15px;
  padding-right: 15px;
  box-shadow: 0 0 0 0 rgba(255, 0, 0, 0.5);
  animation: pulse 1.5s infinite;
}

@media (max-width: 400px) {
  .bottom-menu__call #request_call_btn {
    margin-left: 0px;
    margin-top: 10px;
  }
}

