.banner {
  min-height: 251px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.banner__inner {
  max-width: 1281px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding-left: 10px;
}
